<template>
  <modal :name="name" :click-to-close="false" height="auto" :adaptive="true" :reset="true"
         :scrollable="false" @before-open="onBeforeOpen" classes="v--modal modal-left modal-full-height">

    <div class="vmodal-header pt-4 px-4">
      <h5 class="modal-title">Aller à ...
        <button type="button" class="close" @click="hide">
          <i class="fal fa-times"></i>
        </button>
      </h5>
      <!--<button class="btn z-depth-0 color-4 btn-block bg-transparent" @click="hide">
        <i class="fal fa-times fa-2x"></i>
      </button>-->
      <hr class="w-100 border-color-4">
    </div>

    <div class="vmodal-body p-4">
      <ssf-form-group :select="true" name="day" :row="true" col="col-12" label="Jour" option-field="day"
                      :option-items="days" v-model="result.day"/>

      <ssf-form-group :select="true" name="month" :row="true" col="col-12" label="Mois" option-field="month"
                      :option-items="months" v-model="result.month"/>

      <ssf-form-group :select="true" name="year" :row="true" col="col-12" label="Année" option-field="year"
                      :option-items="years" v-model="result.year"/>
    </div>

    <div class="vmodal-footer py-3 border-top">
      <div class="row w-100">
        <div class="col-6 px-0 border-right">
          <button type="button" class="btn btn-block text-danger" @click="hide">
            <ssf-icon icon="times" weight="light" size="2x"/>
          </button>
        </div>
        <div class="col-6 px-0 border-left">
          <button type="button" class="btn btn-block text-success" @click="save">
            <ssf-icon icon="bullseye-pointer" weight="light" size="2x"/>
          </button>
        </div>
      </div>
    </div>
  </modal>
</template>

<script>
  export default {
    name: "ModalPickDate",

    data() {
      return {
        name  : 'modal-pick-date',
        days  : null,
        months: null,
        years : null,

        result: { day: null, month: null, year: null }
      }
    },

    methods: {
      onBeforeOpen() {
        this.days = this.range(1, 31).map(item => {
          return { day: item }
        })
        this.months = this.range(1, 12).map(item => {
          return { month: item }
        })
        this.years = this.range(2015, 20).map(item => {
          return { year: item }
        })

        let today = this.helpers.date.moment()
        this.result = { day: today.date(), month: today.month() + 1, year: today.year() }
      },

      range(min, nb) {
        return Array.from(Array(nb), (_, i) => i + min)
      },

      hide() {
        this.$modal.hide(this.name)
      },

      save() {
        this.hide()
        return this.$emit('date-picked', this.helpers.date.moment(_.map(this.result, value => this.helpers.number.twoDigits(value)).reverse().join('-'), 'YYYY-MM-DD'))
        // this.$emit('go', `${this.result.year}-${this.helpers.twoDigits(this.result.month)}-${this.helpers.twoDigits(this.result.day)}`)
      }
    }
  }
</script>

