// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue           from 'vue'
import Vue2Filters   from 'vue2-filters'
import VueWebStorage from 'vue-web-storage'
import VueMeta       from 'vue-meta'
import VueJsModal    from 'vue-js-modal'

import cookie from 'json-cookie'
import swal   from 'sweetalert2'

import App     from './App'
import helpers from './app/utils/helpers'
import router  from './app/router'
import store   from './app/store'

import {MainApi} from './app/http/api'

import {SsfIcon}    from 'ssf-icon'
import SsfFormGroup from 'ssf-form-group'
import 'ssf-icon/dist/vue-ssf-icon.css'
import 'ssf-form-group/dist/vue-ssf-form-group.css'

import './app/utils/native/string'

Vue.use(VueJsModal);
Vue.use(Vue2Filters);
Vue.use(VueWebStorage, { prefix: store.getters.storage.prefix });
Vue.use(VueMeta);

Vue.component('ssf-form-group', SsfFormGroup)
Vue.component('ssf-icon', SsfIcon)

window._ = require('lodash');

Vue.config.productionTip = false;

Vue.prototype.api = new MainApi();
Vue.prototype.swal = swal;
Vue.prototype.cookie = cookie;
Vue.prototype.helpers = helpers;


new Vue({
  el    : '#app',
  router,
  store,
  render: h => h(App)/*,
  created() {
    if (sessionStorage.redirect) {
      const redirect = sessionStorage.redirect
      delete sessionStorage.redirect
      this.$router.push({ path: redirect })
    }
  }*/
}).$mount('#app')
