import Vue     from 'vue'
import Vuex    from 'vuex'
import helpers from "../utils/helpers";

Vue.use(Vuex);

const state = {
  APP_NAME       : process.env.APP_NAME || 'kolysko-app', // Nom de l'application et du cookie
  APP_MAINTENANCE: process.env.APP_MAINTENANCE || false, // Maintenance de l'application
  loading        : false, // Le formulaire est envoyé
  preload        : true, // La page est en train de charger
};

const mutations = {
  setLoading(state, loading) {
    state.loading = loading
  },

  setPreload(state, preload) {
    state.preload = preload
  },

};

const actions = {

  loading(context, loading) {
    context.commit('setLoading', loading)
  },

  preloading(context, preload) {
    context.commit('setPreload', preload)
  },

};

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters: {
    APP_NAME       : (state) => state.APP_NAME,
    APP_MAINTENANCE: (state) => state.APP_MAINTENANCE,
    APP_ENV        : () => process.env.API_ENV || 'dev', // Environnement de l'application,
    API_SERVER     : () => process.env.API_SERVER || 'http://localhost:4001', // Adresse du serveur de l'API
    TOKEN_NAME     : () => process.env.TOKEN_NAME || 'kolysko-app_token', // Nom du cookie pour stocker le token,

    loading: state => state.loading,
    preload: state => state.preload,

    storage: state => {
      return { prefix: `storage:${state.APP_NAME}:` }
    },
  }
})
