/**
 * File app/router/index.js
 *
 * Ce fichier contient les routes d'accès à l'app
 *
 * @author sofianeakbly
 **/

import Vue       from 'vue'
import VueRouter from 'vue-router'
import Route     from 'vue-routisan'

Route.setViewResolver((component) => (typeof component).toLowerCase() === 'string' ? require('../../components/pages/' + component).default : component);

import {AuthMiddleware, GuestMiddleware, RouteFoundMiddleware} from '../http/middleware'

import HomeIndex from '../../components/pages/Home/'

import Login          from '../../components/pages/Auth/Login'
import Register       from '../../components/pages/Auth/Register'
import PasswordForgot from '../../components/pages/Auth/Password/Forgot'
import PasswordReset  from '../../components/pages/Auth/Password/Reset'
import Logout         from '../../components/pages/Auth/Logout'

Vue.use(VueRouter);

Route.group({beforeEnter: RouteFoundMiddleware}, () => {
  Route.group({ beforeEnter: AuthMiddleware }, () => {
    /* Page d'accueil */
    Route.view('/', HomeIndex).name('home');
    /* Page de déconnexion */
    Route.view('/logout', Logout).name('logout')

  });

  Route.group({ beforeEnter: GuestMiddleware }, () => {
    /* Page de connexion */
    Route.view('/login', Login).name('login');
    /* Page d'inscription*/
    Route.view('/register', Register).name('register');
    /* Page de mot de passe oublié */
    Route.view('/password/forgot', PasswordForgot).name('password.forgot');
    /* Page de reset mot de passe */
    Route.view('/password/reset/:email', PasswordReset).name('password.reset')
  });
})

Route.redirect('*', '/');

export default new VueRouter({
  routes: Route.all(),
  mode  : 'history'
})
