import helpers   from "@/app/utils/helpers";
import MainError from "@/app/utils/swal/errors/main-error";
import http      from 'axios'
import cookie    from "json-cookie";
import store     from "@/app/store";
import router    from "@/app/router";

class MainApi {
  constructor(server = null) {
    this.server = `${server || (store.getters.API_SERVER + '/api')}`
  }

  config(method, url, data = null, headers = null, file = false) {
    let config = { method, url: `${this.server}${url}`, headers: (headers || {}) };
    if (data) config.data = file ? data : { data };
    if (!config.headers['Content-Type']) config.headers['Content-Type'] = 'application/json';
    config.headers['Authorization'] = `Bearer ${cookie.get(store.getters.TOKEN_NAME)}`;
    return config
  }

  request(method, url, data = null, options = {}) {
    return new Promise((resolve, reject) => {
      helpers.loading();
      http(this.config(method, url, data, options.headers, options.file))
        .then((response) => resolve(response && response.data && response.data.data ? response.data.data : response))
        .catch((error) => {
          let errorMsg = error && error.response && error.response.data && error.response.data.data && error.response.data.data.error ? error.response.data.data.error : "Une erreur est survenue, merci de réessayer plus tard.",
            errorStatus = error && error.response && error.response.status ? error.response.status : 500;
          if (!(options.redirectIfNotLogged === false) && !(options.alert === false))
            MainError.fire({ text: errorMsg }).then(
              () => errorStatus === 401 ? this.redirect().login() : null
            );
          return errorStatus === 401 && !(options.redirectIfNotLogged === false) ? this.redirect().login() : reject(errorMsg)
        })
        .finally(() => helpers.loading(false))
    })
  }

  get(url, options = {}) {
    return new Promise((resolve, reject) => {
      this.request('GET', url, null, options)
        .then((response) => resolve(response))
        .catch((error) => reject(error))
    })
  }

  getBy(url, parameter, options = {}) {
    return this.get(`${url}/${parameter}`, options)
  }

  post(url, data, options = {}) {
    return new Promise((resolve, reject) => {
      this.request('POST', url, data, options)
        .then((response) => resolve(response))
        .catch((error) => reject(error))
    })
  }

  put(url, id, data, options = {}) {
    return new Promise((resolve, reject) => {
      this.request('PUT', `${url}/${id}`, data, options)
        .then((response) => resolve(response))
        .catch((error) => reject(error))
    })
  }

  delete(url, id, options = {}) {
    return new Promise((resolve, reject) => {
      this.request('DELETE', `${url}/${id}`, null, options)
        .then((response) => resolve(response))
        .catch((error) => reject(error))
    })
  }

  redirect() {
    return {
      login: () => {
        store.dispatch('preloading', true);
        router.push({ name: 'login' });
        cookie.delete(store.getters.TOKEN_NAME);
        location.reload()
      }
    }
  }
}

export default MainApi
