<template>
  <auth id="login" page-title="Connexion">

    <template slot="error" v-if="error">{{ error }}</template>

    <template slot="form">

      <form id="formLogin" class="w-100" @submit.prevent="login()">

        <ssf-form-group :row="true" col="col-12" name="email" label="Adresse e-mail" :required="true" icon="at"
                        v-model="user.email"/>

        <ssf-form-group :row="true" col="col-12" name="password" type="password" label="Mot de passe" :required="true"
                        icon="lock" v-model="user.password"/>

        <ssf-form-group :row="true" col="col-12" type="checkbox" name="checkbox" v-model="user.remember"
                        label="Se souvenir de mes informations"/>

        <div class="row">
          <div class="col-12 ssf-form-group text-right mt-1 mb-0">
            <button class="btn bg-color-1 btn-block full-flex" type="submit">
              Laissez-moi entrer
              <ssf-icon icon="door-open" class="position-absolute" style="right: 25px"/>
            </button>
          </div>
        </div>


        <hr class="my-3 my-md-5">

        <div class="font-weight-light">
          <p class="mb-0 text-center font-weight-bold">
            <a href="#" class="color-1" @click.prevent="$router.push({name: 'register'})">
              Nouveau sur Kolysko ? Bienvenue !
              <ssf-icon icon="user-plus" type="la" size="sm"/>
            </a>
          </p>
          <p class="text-center">
            <router-link :to="{name: 'password.forgot'}">
              Mot de passe oublié ?
              <ssf-icon icon="key-skeleton" weight="light"/>
            </router-link>
          </p>
        </div>

      </form>
    </template>

  </auth>
</template>

<script>

  import {AuthApi} from "../../../app/http/api";
  import Icon      from "../../includes/commons/partials/Icon";
  import Auth      from "./Auth";

  export default {
    name      : "Login",
    components: { Auth, Icon },

    metaInfo: { title: 'Connexion', },

    data() {
      return {
        error: null,
        user : { email: null, password: null, remember: false },
      }
    },


    methods: {

      login() {
        this.error = null
        AuthApi.login(this.user).then(() => location.reload()).catch((error) => this.error = error)
      }

    }
  }
</script>

