<template>
  <auth id="resetPassword">

    <template slot="error" v-if="error">{{ error }}</template>

    <template slot="form">

      <form id="formResetPassword" class="w-100" @submit.prevent="send">

        <ssf-form-group :row="true" col="col-12" name="email" v-model="user.email" label="Adresse e-mail"
                        :required="true" icon="at" :disabled="true"/>

        <ssf-form-group :form-group-class="`${errors.password ? 'ssf-form-invalid mb-0' : ''}`" :row="true" col="col-12"
                        name="password" v-model="user.password" label="Mot de passe" type="password" :required="true"
                        :icon="{icon: 'lock-alt', weight: 'light', size: 'sm'}" :autocomplete="true"/>
        <restriction class="col-12" v-if="errors.password"/>

        <ssf-form-group :form-group-class="`${errors.confirm_password ? 'ssf-form-invalid' : ''}`" :row="true"
                        col="col-12" name="confirm_password" v-model="user.confirm_password"
                        label="Confirmation mot de passe" type="password" :required="true"
                        :icon="{icon: 'lock-alt', weight: 'light', size: 'sm'}" :autocomplete="true"/>

        <div class="row">
          <div class="col-12 ssf-form-group mt-1 mb-0">
            <button class="btn bg-color-1 btn-block" type="submit">Enregistrer la modification</button>
          </div>
        </div>
      </form>

    </template>
  </auth>
</template>

<script>
  import {AuthApi}   from "../../../../app/http/api";
  import User        from "../../../../app/models/user";
  import {MainError} from "../../../../app/utils/swal";
  import Auth        from "../Auth";

  import sha1        from 'sha1'
  import Restriction from "./Restriction";

  export default {
    name      : "PasswordReset",
    components: { Restriction, Auth },

    metaInfo: { title: 'Réinitialisation mot de passe', },

    data() {
      return {
        user  : {},
        error : null,
        errors: {}
      }
    },

    created() {
      this.run()
    },

    methods: {

      check() {
        let valid = true
        Object.keys(this.user).map(key => {
          let keyValid = User.check.default(key, this.user)
          valid = valid ? keyValid : false
          this.errors[key] = !keyValid
          if (key === 'confirm_password' && this.user.confirm_password !== this.user.password)
            this.errors[key] = false
        })
        this.$forceUpdate()
        return valid
      },

      send() {
        this.error = null
        if (this.check())
          AuthApi.reset(this.user).then(() => {
            this.$router.push({ name: 'login' })
            // location.reload()
          }).catch((error) => this.error = error)
      },

      run() {
        try {
          if (this.$route.params.email) this.user.email = atob(this.$route.params.email)
        } catch (e) {
          MainError.fire()
        }
      }
    }
  }
</script>

