import moment         from 'moment'
import momentTimezone from 'moment-timezone'
import helpers        from "./helpers";

class DateJs {

  constructor() {
    momentTimezone.locale('fr');
    momentTimezone.tz.setDefault('Europe/Paris');
  }

  static moment(date = null, format = 'YYYY/MM/DD') {
    momentTimezone.locale('fr');
    momentTimezone.tz.setDefault('Europe/Paris');
    return date === null ? moment() : format ? moment(date, format) : moment(date)
  }

  static momentSql(date = null, format = 'YYYY-MM-DD HH:mm:ss') {
    return this.moment(date, format)
  }

  static age(birthday, unit = true) {
    let year, month, week;
    return unit || (year = this.moment().diff(this.moment(birthday), 'years')) > 0
      ? unit ? `${year} an${Math.abs(year) > 1 ? 's' : ''}` : year
      : (month = this.moment().diff(this.moment(birthday), 'month')) > 0
        ? `${Math.abs(month)} mois`
        : `${week = this.moment().diff(this.moment(birthday), 'week')} semaine${Math.abs(week) > 1 ? 's' : ''}`
  }

  static from(date) {
    let minutes = Math.abs(this.diff(this.moment(), this.moment(date), false)), hours,
      when = this.moment() > this.moment(date) ? "il y a" : "dans", duration, unit;
    if (minutes < 60) {
      duration = minutes;
      unit = 'minutes'
    } else if ((hours = (minutes / 60)) < 24) {
      duration = hours;
      unit = 'heures'
    } else {
      duration = hours / 24;
      unit = 'jours'
    }

    return { when, duration, unit }
  }

  static diff(start = null, end = null, format = true) {
    let minutes = this.moment(end).diff(this.moment(start), 'minutes')
    return start && end ? format ? this.formatHoursAndMinutes(parseInt(minutes / 60), parseInt(minutes % 60)) : minutes : null
  }

  static formatHoursAndMinutes(hours, minutes, separator = 'h') {
    return `${helpers.number.twoDigits(hours)}${separator}${helpers.number.twoDigits(minutes)}`
  }

  static formatTime(date, separator = 'h') {
    return this.formatHoursAndMinutes(date.hours(), date.minutes(), separator)
  }

}

export default DateJs
