<template>
  <div id="app">

    <!-- APPLICATIN IS UP -->
    <div class="h-100 w-100" v-if="!maintenance">
      <!-- MAIN CONTAINER -->
      <div class="main-container">
        <!--        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">-->
        <router-view v-if="loaded" class="main-container"/>
        <!--        </transition>-->
      </div>

      <!-- PRE LOAD -->
      <pre-load/>

      <!-- FORM LOAD-->
      <form-load/>

      <!-- FOOTER -->
      <!--      <x-footer/>-->
    </div>

    <!-- MAINTENANCE MODE -->
    <maintenance v-else-if="maintenance"/>

  </div>
</template>

<script>

  import FormLoad    from '@/components/includes/commons/FormLoad'
  import PreLoad     from '@/components/includes/commons/PreLoad'
  import XFooter     from '@/components/includes/commons/partials/XFooter'
  import Maintenance from '@/components/includes/Maintenance'

  export default {
    name: 'App',

    metaInfo: {
      // if no subcomponents specify a metaInfo.title, this title will be used
      title        : 'Application | Slogan',
      // all titles will be injected into this template
      titleTemplate: '%s | App'
    },

    components: {
      Maintenance,
      FormLoad,
      PreLoad,
      XFooter,
    },

    data() {
      return {
        // loaded: false,
      }
    },

    computed: {
      loaded() {
        return !this.$store.getters.preload
      },

      maintenance() {
        return this.$store.getters.APP_MAINTENANCE && (this.$store.getters.APP_MAINTENANCE === 'true' || this.$store.getters.APP_MAINTENANCE === true)
      }
    },

    mounted() {
    },

    methods: {

      onCustomAction(action) {
        return eval(this[action]())
      },

      navigate(name) {
        return this.$router.push({ name })
      },

    }
  }
</script>

