import Model    from "@/app/models/model";
import helpers  from "@/app/utils/helpers";
import moment   from "moment";
import momentTz from 'moment-timezone'

class Holiday extends Model {
  static table = 'furlough';

  static period(date = null, year = null) {
    return super.all(`/${this.table}/${year || moment().year()}/${btoa(encodeURIComponent(date || moment()))}`)
  }

  static half(holiday) {
    return holiday.half
  }

  static findInHolidays(holidays, date) {
    for (let i = 0; i < (holidays ? holidays.length : 0); i++)
      if (moment(holidays[i].date).format('l') === moment(date).format('l'))
        return holidays[i];
    return null
  }

  static store(holiday) {
    holiday = helpers.clone(holiday);
    holiday.date = moment(holiday.date, 'DD/MM/YYYY').add(6, 'hours');
    holiday.start = moment(holiday.start, 'DD/MM/YYYY').add(6, 'hours');
    holiday.end = moment(holiday.end, 'DD/MM/YYYY').add(6, 'hours');
    return super.store(`/${this.table}`, holiday)
  }
}

export default Holiday
