import sha1 from 'sha1';

import helpers from "../../../utils/helpers";
import User    from "../../../models/user";

import MainApi from './main-api';

class AuthApi {

  static login(data) {
    let user = helpers.clone(data);
    user.password = sha1(data.password);

    this.deleteUserData()

    return new Promise((resolve, reject) => {
      (new MainApi()).post('/login', user, { redirectIfNotLogged: false })
        .then((response) => {
          this.setTokenAndStorage(response)
          return resolve(response)
        })
        .catch((error) => reject(error))
    })

  };

  static register(data, url = '/register') {
    let user = helpers.clone(data)
    user.password = sha1(data.password)
    delete user.confirm_password

    return new Promise((resolve, reject) => {
      if (data.password !== data.confirm_password)
        return reject("Les mots de passes ne correspondent pas");
      if (!User.isStrongPassword(data.password))
        return reject("Le mot de passe doit contenir 8 caractère minimum dont au moins : 1 chiffre, 1 majuscule, 1 minuscule et 1 caractère spécial");
      (new MainApi()).post(url, user, { redirectIfNotLogged: false })
        .then((response) => {
          // this.setTokenAndStorage(response)
          (new MainApi()).redirect().login()
          return resolve(response)
        })
        .catch((error) => reject(error))
    })
  };

  static forgot(data) {
    return new Promise((resolve, reject) => {
      (new MainApi()).post('/password/forgot', data, { redirectIfNotLogged: false })
        .then((response) => resolve(response))
        .catch((error) => reject(error))
    })
  }

  static reset(data) {
    return this.register(data, '/password/reset');
  }

  static logout() {
    return new Promise((resolve, reject) => {
      (new MainApi()).post('/logout', null, { redirectIfNotLogged: false })
        .then((response) => resolve(response))
        .catch((error) => reject(error))
        .finally(() => User.deleteTokenAndStorage())
    })
  }

  static setTokenAndStorage(data) {
    return data ? User.setTokenAndStorage(data.token, data.user) : null
  }

  static deleteUserData() {
    return User.deleteTokenAndStorage()
  }
}

export default AuthApi
