<template>
  <div id="homeIndex">

    <ssf-calendar :today="date" :year="year" :holidays="holidays" @nav-date="onNavDate" @selected="onSelected"
                  @date-picked="onDatePicked"/>

    <div class="row mt-5 mt-md-4" id="legend" v-if="categories">
      <div class="col-2 ol-md-2 text-center mb-1" v-for="category in categories">
        <div class="type d-block m-auto rounded-circle pb-2">
          <span class="full-flex mx-auto category-cell" :style="`background: ${category.color};`" data-toggle="tooltip"
                data-placement="top" :data-original-title="category.label.capitalize()">
            <span class="text-white">{{ category.label.split(' ').map(word => word[0]).join('') | uppercase }}</span>
          </span>
        </div>
        <span class="font-italic small" :style="`color: ${category.color}`">{{ perCategory(category) }}</span>
      </div>
    </div>

    <btn-floating-container @logout="logout" @pick-date="onPickDate"/>

    <modal-add-holiday :categories="categories" :holidays="holidays" @refresh="fetchHolidays"/>

    <modal-pick-date @date-picked="onDatePicked"/>

  </div>
</template>

<script>
  import AuthApi              from "@/app/http/api/models/auth-api";
  import Category             from "@/app/models/category";
  import Holiday              from "@/app/models/holiday";
  import Period               from "@/app/models/period";
  import {daysOut}            from "@/app/utils";
  import BtnFloatingContainer from "@/components/pages/Home/components/includes/BtnFloatingContainer";
  import ModalAddHoliday      from "@/components/pages/Home/components/includes/ModalAddHoliday";
  import ModalPickDate        from "@/components/pages/Home/components/includes/ModalPickDate";
  import SsfCalendar          from "../../../includes/Calendar";

  export default {
    name      : "HomeIndex",
    components: { ModalAddHoliday, ModalPickDate, BtnFloatingContainer, SsfCalendar },
    metaInfo  : {
      title: 'Accueil',
    },

    data() {
      return {
        categories: null,
        holidays  : null,
        period    : null,

        year: null,
        date: null
      }
    },

    created() {
      this.run()
    },

    mounted() {
    },

    methods: {
      async run() {
        this.date = this.helpers.date.moment()
        this.year = this.date.year()
        this.categories = await Category.all()
        this.period = await Period.mine()
        this.fetchHolidays()
        $('[data-toggle="tooltip"]').tooltip()
      },

      onNavDate(date, direction) {
        this.date = date
        this.year = this.date.year()
        if ((direction < 0 && parseInt(this.date.format('MM')) === parseInt(this.period.end.split('-')[0]))
          || (direction > 0 && parseInt(this.date.format('MM')) === parseInt(this.period.start.split('-')[0]))
          || direction === 0)
          this.fetchHolidays()
      },

      async fetchHolidays() {
        this.holidays = await Holiday.period(this.date, this.year)
      },

      perCategory(category) {
        return this.holidays ? _.filter(this.holidays, holiday => holiday.category.id === category.id).length - _.filter(this.holidays, holiday => holiday.category.id === category.id && holiday.half).length / 2 : 0
      },

      goToDate(date) {
        this.currentDay = this.helpers.moment(date)
        this.refill()
        $('#modalPickDate').modal('hide')
      },

      openPickDate() {
        $('#modalPickDate').modal('show')
      },

      logout() {
        AuthApi.logout().then(() => location.reload())
      },

      exp() {
        /*this.$store.commit('setFormLoad', true)
        this.api.get('/export/' + this.helpers.moment().year()).then((r) => {
          this.$store.commit('setFormLoad', false)
          if (r.data.filename)
            location.replace(`${this.$store.getters.API_SERVER}${r.data.filename}/${btoa(this.cookie.get(this.$store.getters.JWT_TOKEN))}`)
          else
            this.swal({
              title: 'Oooops...', text: 'Veuillez réessayer plus tard', type: 'warning'
            })
        }, (e) => {
          this.$store.commit('setFormLoad', false)
          this.swal({
            title: 'Oooops...', text: 'Veuillez réessayer plus tard', type: 'warning'
          })
        })*/
      },

      onPickDate() {
        this.$modal.show('modal-pick-date')
      },

      onDatePicked(date) {
        this.onNavDate(date, 0)
      },

      onSelected(date) {
        this.$modal.show('modal-add-holiday', {
          date
        })
      }
    }
  }
</script>

<style lang="scss">

  .date-cell.week-end:not(.cell-previous-month) {
    /*color: #fff;*/
    background: #FF3B3F;
    background-image: repeating-linear-gradient(-45deg, #fff 3px, #fff 6px, #FF3B3F 7px, #FF3B3F 14px);
  }

  .date-cell.out:not(.cell-previous-month) {
    /*color: #fff;*/
    background: #ff0;
    background-image: repeating-linear-gradient(-45deg, #fff 3px, #fff 6px, #ff0 7px, #ff0 14px);
  }

  .date-cell.half {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      height: 3px;
      background: #FFF;
      transform: rotate(-45deg);
    }

    /*border: 2px dotted #000 !important;*/
  }

  .category-cell {
    font-weight: 500;
    height: 35px;
    width: 35px;
    border-radius: 50%;

    &, & * {
      cursor: default;
    }
  }

</style>
