<template>
  <auth id="register" page-title="Inscription">

    <template slot="error" v-if="error">{{ error }}</template>

    <template slot="form">
      <form id="formRegister" class="w-100" @submit.prevent="register()" novalidate>

        <div class="row">

          <ssf-form-group :form-group-class="`${errors.lastname ? 'ssf-form-invalid' : ''}`" col="col-12 col-md-6"
                          name="lastname" v-model="user.lastname" label="Nom" :required="true"
                          :icon="{icon: 'user', type: 'la'}"/>

          <ssf-form-group :form-group-class="`${errors.firstname ? 'ssf-form-invalid' : ''}`" col="col-12 col-md-6"
                          name="firstname" v-model="user.firstname" label="Prénom" :required="true"
                          :icon="{icon: 'user', type: 'la'}"/>

        </div>

        <ssf-form-group :form-group-class="`${errors.email ? 'ssf-form-invalid' : ''}`" :row="true" col="col-12"
                        name="email" v-model="user.email" label="Adresse e-mail" :required="true"
                        :icon="{icon: 'at', type: 'la'}"/>

        <ssf-form-group :form-group-class="`${errors.password ? 'ssf-form-invalid mb-0' : ''}`" :row="true" col="col-12"
                        name="password" v-model="user.password" label="Mot de passe" type="password" :required="true"
                        :icon="{icon: 'lock-alt', weight: 'light', size: 'sm'}" :autocomplete="true"/>
        <restriction class="col-12" v-if="errors.password"/>

        <ssf-form-group :form-group-class="`${errors.confirm_password ? 'ssf-form-invalid' : ''}`" :row="true"
                        col="col-12" name="confirm_password" v-model="user.confirm_password"
                        label="Confirmation mot de passe" type="password" :required="true"
                        :icon="{icon: 'lock-alt', weight: 'light', size: 'sm'}" :autocomplete="true"/>

        <div class="row">
          <div class="col-12 ssf-form-group text-right mt-1 mb-0">
            <button class="btn bg-color-1 btn-block full-flex" type="submit">
              Débuter l'expérience
              <ssf-icon icon="person-booth" type="la" size="lg" class="position-absolute" style="right: 25px"/>
            </button>
          </div>
        </div>

        <div class="row">
          <hr class="w-50">
          <div class="col-12 ssf-form-group mt-1 text-center font-weight-bold">
            <router-link :to="{name: 'login'}" class="color-1">
              Déja utilisateur ? Connecte-toi !
              <ssf-icon icon="door-open" type="la" size="lg"/>
            </router-link>
          </div>
        </div>

      </form>
    </template>

  </auth>
</template>

<script>

  import {AuthApi}   from "../../../app/http/api";
  import User        from "../../../app/models/user";
  import Auth        from "./Auth";
  import Restriction from "./Password/Restriction";

  export default {
    name: "Register",

    components: { Restriction, Auth },

    metaInfo: { title: 'Inscription', },

    data() {
      return {
        error : null,
        user  : { firstname: null, lastname: null, email: null, password: null, confirm_password: null },
        errors: {}
      }
    },

    methods: {
      check() {
        let valid = true
        Object.keys(this.user).map(key => {
          let keyValid = User.check.default(key, this.user)
          valid = valid ? keyValid : false
          this.errors[key] = !keyValid
          if (key === 'confirm_password' && this.user.confirm_password !== this.user.password)
            this.errors[key] = false
        })
        this.$forceUpdate()
        return valid
      },

      register() {
        this.error = null
        if (this.check())
          AuthApi.register(this.user).then(() => location.reload()).catch((error) => this.error = error)
      }
    }
  }
</script>

