<template>
  <modal :name="name" :click-to-close="false" height="auto" :adaptive="true" :reset="true"
         :scrollable="false" @before-open="onBeforeOpen" classes="v--modal modal-right modal-full-height">

    <div class="vmodal-header pt-4 px-4">
      <button class="btn z-depth-0 color-4 btn-block bg-transparent" @click="hide">
        <ssf-icon icon="calendar-alt" size="2x" weight="light"/>
      </button>
      <hr class="w-100">
    </div>

    <div class="vmodal-body p-4">

      <ssf-form-group :select="true" name="category_id" :row="true" col="col-12" label="Catégorie" option-field="id"
                      option-label="label" :option-items="computedCategories" v-model="holiday.category_id"
                      v-if="categories"/>

      <div class="row">
        <ssf-form-group type="date" name="start" col="col-12 col-md-6" label="Début" v-model="holiday.start"/>
        <ssf-form-group type="date" name="end" col="col-12 col-md-6" label="Fin" v-model="holiday.end"
                        class="holiday-end"/>
      </div>

      <ssf-form-group type="checkbox" name="half" v-model="holiday.half" label="Demie-journée" :row="true"
                      col="col-12"/>
    </div>

    <div class="vmodal-footer py-3 border-top">
      <div class="row w-100">
        <div class="col-6 px-0 border-right">
          <button type="button" class="btn btn-block text-danger" @click="hide">
            <ssf-icon icon="times" weight="light" size="2x"/>
          </button>
        </div>
        <div class="col-6 px-0 border-left">
          <button type="button" class="btn btn-block text-success" @click="save">
            <ssf-icon icon="save" weight="light" size="2x"/>
          </button>
        </div>
      </div>
    </div>
  </modal>
</template>

<script>
  import Holiday from "@/app/models/holiday";

  export default {
    name: "ModalAddHoliday",

    props: {
      categories: { required: true },
      holidays  : { required: true },
    },

    data() {
      return {
        name  : 'modal-add-holiday',
        days  : null,
        months: null,
        years : null,

        holiday: {}
      }
    },

    computed: {
      computedCategories() {
        return this.categories.filter(category => category.label = category.label.capitalize()).concat([{
          id   : -1,
          label: 'Aucun',
          name : 'nothing'
        }])
      }
    },

    methods: {
      onBeforeOpen(event) {
        let date = this.helpers.date.moment(event.params.date).add(6, 'hours');
        this.holiday = this.helpers.clone(Holiday.findInHolidays(this.holidays, date) || {
          date       : date,
          start      : date,
          end        : date,
          category_id: null,
          half       : null
        });
        this.holiday.date = this.helpers.date.moment(this.holiday.date).format('DD/MM/YYYY');
        this.holiday.start = this.helpers.clone(this.holiday.date);
        this.holiday.end = this.holiday.date > this.holiday.end ? this.helpers.clone(this.holiday.start) : this.helpers.date.moment(this.holiday.end).format('DD/MM/YYYY');
        this.$forceUpdate()
      },

      range(min, nb) {
        return Array.from(Array(nb), (_, i) => i + min)
      },

      hide() {
        this.$modal.hide(this.name)
      },

      save() {
        // this.hide()
        Holiday.store(this.holiday).then((response) => {
          if (response) {
            this.hide();
            return this.$emit('refresh')
          }
        })
        // return this.$emit('date-picked', this.helpers.date.moment(_.map(this.result, value => this.helpers.number.twoDigits(value)).reverse().join('-'), 'YYYY-MM-DD'))
        // this.$emit('go', `${this.result.year}-${this.helpers.twoDigits(this.result.month)}-${this.helpers.twoDigits(this.result.day)}`)
      }
    }
  }
</script>

