  .modal-right {
    top: 10px !important;
    left: 10px !important;
    right: 10px !important;
    width: calc(100% - 20px) !important;
    padding-bottom: 90px;
    overflow: visible !important;
  }

  .vmodal-footer {
    position: absolute;
    left: 0 !important;
    right: 0 !important;
    bottom: 0 !important;
    width: 100%;
  }

  .holiday-end .ssf-date-container .ssf-date-input-container.open {
    left: inherit !important;
    right: 0 !important;
  }

  @media (min-width: 992px) {

    .modal-right {
      top: inherit;
      right: 0 !important;
      left: inherit !important;

      &.modal-full-height {
        position: absolute;
        margin: 0;
        width: 500px !important;
        min-height: 100vh !important;
        height: 100vh !important;
        top: 0 !important;
        left: 0;
      }
    }
  }

</style>
