<template>
  <auth id="forgotPassword" page-title="Réinitialisation mot de passe">

    <template slot="error" v-if="error">{{ error }}</template>
    <template slot="success" v-if="success">{{ success }}</template>

    <template slot="form">
      <form id="formForgotPassword" class="w-100" @submit.prevent="send">

        <ssf-form-group :row="true" col="col-12" name="email" v-model="user.email" label="Adresse e-mail"
                        :required="true" autocomplete="email" icon="at"/>

        <div class="row">
          <div class="col-12 ssf-form-group text-right mt-1 mb-0">
            <button class="btn bg-color-1 btn-block" type="submit">Envoyer le lien de réinitialisation</button>
          </div>
        </div>

        <div class="row">
          <hr class="w-50">
          <div class="col-12 ssf-form-group mt-1 text-center font-weight-bold">
            <router-link :to="{name: 'login'}" class="color-1">
              Retour à la connexion
              <ssf-icon icon="door-open" type="la" size="lg"/>
            </router-link>
          </div>
        </div>

      </form>
    </template>

  </auth>
</template>

<script>

  import {AuthApi} from "../../../../app/http/api";
  import Auth      from "../Auth";

  export default {
    name      : "PasswordForgot",
    components: { Auth, },

    metaInfo: { title: 'Mot de passe oublié', },

    data() {
      return {
        error  : null,
        success: null,
        user   : { email: null },
        links  : [
          { icon: 'user-plus', label: "Je souhaite m'inscrire", route: 'register' },
          { icon: 'sign-in', label: "Je souhaite me connecter", route: 'login' },
        ]
      }
    },

    methods: {

      send() {
        this.error = null
        this.success = null
        AuthApi.forgot(this.user)
          .then(() => this.success = "Veuillez suivre les instructions reçues par mail. (Vérifiez vos spams)")
          .catch((error) => this.error = error)
      }

    }
  }
</script>

